<script>
import Tips from './Tips';
export default {
  components: {
    "tips": Tips
  },
  data() {
    return {
      contenttips:"",
    };
  },
  methods: {
    fork: function() {
      const forkContent = this.$refs.forkContent.value;
      function strlen(str) {
        var len = 0;
        for (var i = 0; i < str.length; i++) {

          var a = str.charAt(i);
          if (a.match(/[^\x00-\xff]/ig) != null) {
            len += 2;
          } else {
            len += 1;
          }
        }
        return len;
      }
      if (strlen(forkContent)>48) {
        this.contenttips = '2448,'+strlen(forkContent);
        return;
      }

      const forkPrice = this.$refs.forkPrice.value
      const forkDeposit = this.$refs.forkDeposit.value
      
      const data = Object.assign({}, { forkPrice, forkDeposit, forkContent })
      this.$root.forkAdBoard(data)
    },
  }
};
</script>

