<script>
import $ from "jquery";
import TopNavigation from "./Sections/TopNavigation";
export default {
  name: "HowToPlay",
  components: {
    "top-navigation": TopNavigation
  },
  mounted:function() {
    $(".showmore").click(function(){
      if(!$(this).next("div").is(':visible')){
        $(this).next("div").slideDown();
        $(this).find("span").html("-");
      }else{
        $(this).next("div").slideUp();
        $(this).find("span").html("+");
      }
    });
  }
}
</script>

